import React from "react";
import "./App.css";
import AboutMe from "./Components/aboutme";
import Navbar from "./Components/navbar";

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      highest: 0,
      position: "relative",
      padding: "-20px"
    };
  }

  componentDidMount() {
    window.addEventListener("scroll", this.set_position_header, false);
  }

  componentWillUnmount = () => {
    window.removeEventListener("scroll", this.set_position_header);
  };

  // Sets the position of the nav bar. Scrolling down = false; Scrolling up = true.
  set_position_header = () => {
    let offsetY = window.pageYOffset;

    if (offsetY == 0) {
      this.setState({
        position: "relative",
        padding: "-20px"
      });
    } else if (offsetY < this.state.highest) {
      this.setState({
        highest: offsetY,
        position: "fixed",
        padding: "-20px"
      });
    } else {
      this.setState({
        highest: offsetY,
        position: "relative"
      });
    }
  };

  render() {
    return (
      <div className="App">
        <header className="App-header">
          <Navbar links={this.state}></Navbar>
        </header>

        <section>
          <AboutMe></AboutMe>
        </section>
      </div>
    );
  }
}

export default App;
